.chip{
    font-size: 14px!important;
    margin-bottom: 12px!important;

}

.company-detail-input input{
    padding: 12px 10px;
    background: white;
    font-size: 13px;
    height: 18px;
}

.company-detail-input label{
    font-size: 14px;
    top: -7px;

}

.company-detail-input label[data-shrink=true]{
    top:0px;
}

.company-details-each-section{
    padding-bottom: 10px;
}
.company-detail-input{
    margin-top: 10px!important;
}
.input-label{
    font-size: 15px;
    margin-bottom: 0px;
    margin-top: 10px;
}

.slider-dashboard{
    margin-top: 20px;

}

.slider-container-dashboard>div{
    height: 10px;
    border-radius: 5px;
    -webkit-box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.3);
    box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.3);
}
.slider-container-dashboard>div:first-child{
    background: #00ff00;

}
.slider-container-dashboard>div:last-child{
    background: #a8a8a8;

}
.slider-container-dashboard button{
    width: 19px;
    height: 19px;
    background: #296808;
    top: 50%;

}

.slider-range {
    margin-top: 8px;
    color: #b4b4b4;
}

.annual-revenue-resault{
    margin-top: 15px;
    font-size:14px;
}
.annual-revenue-resault span:last-child{
    display: inline-block;
    margin-left: 5px;
    padding-left: 3px;
    padding-right: 3px;
    font-size: 19px;
    font-weight: 600;
    /*border-bottom: 1px dotted blue;*/
}

.progress-bar-dashboard-text text {
    font-size: 12px;
}
.progress-bar-dashboard-text span{
    font-size: 14px;
}
.progress-bar-dashboard-text>span:last-child>span{
    margin-top: 5px;
}

.center-horizantal{
    display: flex;
    align-items: center;
}

.input-simulator{
    padding: 6px 10px;
    border: 1px solid #7b7878;
    border-radius: 5px;
    font-size: 18px;
}

.margin-bottom-feild{
    margin-bottom: 25px;
}

.change-input-btn{
    font-size: 22px;
    width: 30px;
    cursor: pointer;
    display:inline-block;
}

.cant-clicked.change-input-btn{
    color: #c1c1c1;
}
.price-unit{
    font-size: 16px;
    font-weight: 600;

}

.company-detail-header{
    margin-bottom: 25px;
}

.change-btn-style{
    padding: 0px!important;
    min-width: auto!important;
    min-height: auto!important;
}
.change-btn-style:hover{
    background:transparent!important;
}

.change-input-btn:active{
    background: #eaeaea;
    border-radius: 5px;
}
.timeline-container{
    margin: 0 auto;
    background: #DCDCDC!important;

}

.each-timeline {
    font-size: 15px!important;
    border-right: 1px solid #c1c1c1!important;
}
.each-timeline:last-child{
    border-right:1px solid transparent!important ;
}


.StripeElement {
    background-color: white;
    height: 40px;
    padding: 10px 12px;
    border-radius: 4px;
    border: 1px solid transparent;
    box-shadow: 0 1px 3px 0 #e6ebf1;
    -webkit-transition: box-shadow 150ms ease;
    transition: box-shadow 150ms ease;
}

.StripeElement--focus {
    box-shadow: 0 1px 3px 0 #cfd7df;
}

.StripeElement--invalid {
    border-color: #fa755a;
}

.StripeElement--webkit-autofill {
    background-color: #fefde5 !important;
}
.payment-element label{
    width: 100%;
    margin-bottom: 5px;
}

.payment-element{
    margin-bottom: 15px;
}

.payment-element label>div{
    margin-top: 5px;
}
.payment-progress-btn{
    width: 84px;
    font-size: 14px!important;
    background-color: #07af07!important;
}

.payment-progress-btn:hover{
    background-color: green!important;
}